.sub-text {
    color: red;
    font-weight: 700
}

.switcher-wrap ul {
    margin: 0;
    padding: 0;
    list-style: none
}

.switcher-wrap .color-switcher ul li {
    width: 28px;
    height: 28px;
    float: left;
    margin: 3px 3px 10px;
    cursor: pointer;
    transition: .3s
}

.switcher-wrap .color-switcher ul li.active {
    border: 3px solid #455a64
}

.switcher-wrap .switcher-trigger {
    position: absolute;
    left: 100%;
    background: #263238;
    top: 0;
    color: rgba(255, 23, 68, .5);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    border-radius: 0 2px 2px 0
}

@media screen and (max-width:768px) {
    .switcher-wrap {
        display: none
    }
}

.hero,
.logo,
.sub-text {
    display: flex
}

.hero {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70vh;
    background-color: #fdeeed;
    margin: 0;
    font-family: Staatliches, sans-serif;
    padding-top: 80px
}

.logo {
    flex-direction: column;
    align-items: center;
    border: 4px solid red;
    border-radius: 10px;
    background-color: #fff;
    padding: 20px 40px;
    text-align: center
}

.logo h1 {
    font-size: 40px;
    margin: 0
}

.sub-text {
    width: 100%;
    font-size: 16px;
    margin-top: 10px;
    border-top: 3px solid red;
    padding-top: 10px
}

.sub-text span {
    flex-grow: 1
}

.sub-text span:last-child {
    text-align: right;
    margin-left: 10px
}

.hero .logo {
    text-align: center;
    margin-bottom: 20px
}

.hero .btn {
    display: block;
    margin: 0 auto
}

.smart-scroll nav.navbar {
    background-color: #fff;
    transition: .3s
}

#contact-link {
    color: #e80000;
    font-weight: 700
}

#contact-link:hover {
    color: #fdeeed;
    font-weight: 700
}